import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'
import Table from './views/nav1/Table.vue'
import Form from './views/nav1/Form.vue'
import user from './views/nav1/user.vue'
import Page4 from './views/nav2/Page4.vue'
import Page5 from './views/nav2/Page5.vue'
import Page6 from './views/nav3/Page6.vue'
import echarts from './views/charts/echarts.vue'
import Comment from './views/nav1/Comment.vue'
import Banner from './views/nav1/Banner.vue'
import Branch from './views/nav1/Branch.vue'
import MalfunctionDetail from './views/nav1/MalfunctionDetail.vue'
import Order from './views/nav1/Order.vue'
import Recycle from './views/nav2/Recycle.vue'

let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    
    //{ path: '/main', component: Main },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-message',//class
        children: [
            { path: '/main', component: Main, name: 'main', hidden: true },
            { path: '/table', component: Table, name: '' },
            { path: '/form', component: Form, name: '' },
            { path: '/comment', component: Comment, name: '' },
            { path: '/banner', component: Banner, name: ''},
            { path: '/branch', component: Branch, name: ''},
            { path: '/malfunctiondetail', component: MalfunctionDetail, name: ''},
            { path: '/order', component: Order, name: ''}
        ]
    },
    
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-id-card-o',
        children: [
            { path: '/recycle', component: Recycle, name: '' },
            { path: '/page4', component: Page4, name: '' },
            { path: '/page5', component: Page5, name: '' }
            
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-address-card',
        leaf: true,//
        children: [
            { path: '/page6', component: Page6, name: '' }
        ]
    },
    
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }
];

export default routes;


